
import React, { useEffect, useState, useRef } from "react";
import { getStore, gen16CodeId, ObjectKeys,
	} from "../../../stores/Utils";

import {DropInput_Cmpt } from "../../../components";
import { getall_notaryAgent } from "../services/fetch_service";

const DropInput = DropInput_Cmpt();


const collName = "notaryAgents_list"



const SelectNotaryAgents  = ({ handleClick }) => {

	
	const [initialize, setInitialize] = useState(false);
	const [obs, set_obs] = useState(0);
	const [loading, set_loading] = useState(false);
	const [list, setList] = useState([]);

	const searchRef = useRef();

	useEffect(() => {
		if (!initialize) {
			setInitialize(true);
			getall_notaryAgent(cb_result)
			set_obs(gen16CodeId())
		}
    });


    const cb_result =  (v) => {
		set_obs(gen16CodeId())
	}

	const handleChange =  (v) => {
		!loading && set_loading(true)
	}

	const handle_search =  (v) => {
		setList(filterArr(getStore(collName),v))
		loading && set_loading(false)
	}

	const handle_empty =  (v) => {
		setList([]);
		loading && set_loading(false)
	}
	
	
    
    const selectItem =  (acc,subacc) => {
		handleClick(acc,subacc);
		searchRef.current.clearTx();
	}

	

    return (
		<div>
			<DropInput
				placeholder={"buscar..."} 
				width={420}
				fetchData={handle_search}
				loading={loading}
				change={handleChange}
				callempty={handle_empty}
				ref={searchRef}
			>
				<div className="">
					{list && ObjectKeys(list)?.map(cid=>{
						return (
							<Item 
								data={list[cid]}
								elmId={cid} 
								key={cid}
								selectItem={selectItem}
							/>
						)
					})}
				</div>
			</DropInput>
		</div>
    )   
}

export default SelectNotaryAgents;




const Item  = ({ data,  selectItem }) => {
	
	return (
		<div className="item_acc">
			<div className="_dsplFlx item_box account_header">
				<div className="_dsplFlx"  onClick={()=>selectItem(data, null)}>
					<div className="code" >
						{data?.name} 
					</div>
				</div>
			</div>
			
		</div>
	)
}






export function filterArr(arr1,search) {
	let srch = search.toLowerCase();
    var newArr = [];
    if (arr1.length > 0) {
		arr1.map((s) => {
			if (s["name"]?.toLowerCase()?.indexOf(srch)>=0 ) {
				newArr.push(s);
			}
		})
	}
	return newArr;
}