import SelectNotaryAgents from "./SelectNotaryAgent";
import { useState } from "react";
import { CloseModal } from "../../../components/DialogHRM";
import { TextInput_Cmpt } from "../../../components";
import { gen16CodeId, fetchEncryptStream, getStore, qryStore } from "../../../stores/Utils";


const TextInput = TextInput_Cmpt();




const NotaryAgentPick  = ({ data, confirm }) => {


    const [name, set_name] = useState("");
    const [addview, set_addview] = useState(false);


	const handleSearch = (v) => {
        set_name(v?.name)
	}


	
	

	

	const save = async() => {
        if(addview){
            let nnfrm = {name: name, notaryAgentId: gen16CodeId()};
            let bdyq2 = {...getStore(qryStore())?.add_notaryAgn};
			bdyq2.params = {
				businessId:  getStore("profileUser")?.businessId
			}
            bdyq2.form=nnfrm;
            const res = fetchEncryptStream(bdyq2);
			const td = await res;
			if (td && !td.error) {
                confirm && confirm(name);
                data?.modalID && CloseModal({id:data?.modalID});
            }
            
        }else{
            confirm && confirm(name);
            data?.modalID && CloseModal({id:data?.modalID});
        }
	};


	
    const add = () => {
        set_addview(true)
    }

	
	return(
		<div style={{
			maxHeight: "90vh",
			minHeight:  "88vh",
			width: "560px",
			overflow: "auto",
			background: "#fff",
			borderRadius: 13,
			padding: "10px 19px"
		}}>
			<div className="_dsplFlx ">
				<p className="form_title">
					{addview?"Agregar ":""} Agente de Notaria
				</p>
				<div className="flexSpace"/>
				<p className="group_title addBtn purple" onClick={add}>
					Agregar
				</p>
			</div>
            {!addview?
			<div  style={{margin: "18px 0"}}>
                {name?
                <div>
                    
                    {name}
                    
                    </div>
                    :
                    <SelectNotaryAgents
                        placeholder={"buscar..."} 
                        width={420}
                        handleClick={handleSearch}
                    />
                }
            </div>
            :
                <div  style={{margin: "18px 0"}}>
                    <TextInput  
                        init={name} 
                        label={"Nombre"} 
                        width={360} 
                        updChanges={(e)=>set_name(e)}
                    />
                </div>
            }
		<div className="_dsplFlx ">
				<div className="flexSpace"/>
				<p className="group_title addBtn purple" onClick={save}>
						Guardar
				</p>
			</div>
	</div>
	)

}



export default NotaryAgentPick;